import React, { FC, useState, useEffect, useRef } from "react"
import { Helmet } from "react-helmet"
import { analyticsTrack } from "../utils"
import { OutboundLink } from "gatsby-plugin-google-analytics"
import {
  Layout,
  LoadingScreen,
  Participant,
  ParticipantForm,
  Popup,
} from "../components"
import Iphones from "../assets/iphones.svg"
import easterAdvertise from "../assets/easter-advertise.png"
import { Review } from "../components/Review"
import Review1 from "../assets/img/review-1.svg"
import Review2 from "../assets/img/review-2.svg"
import Review3 from "../assets/img/review-3.svg"
import Review4 from "../assets/img/review-4.svg"
import Review5 from "../assets/img/review-5.svg"

interface State {
  loading: boolean
  participants?: any
  group?: {
    name: string
    comments: string
  }
}

const Content = ({ participants, group, showFriendOnClick }) => {
  const [secretFriend, setSecretFriend] = useState([])

  const [
    shuffledparticipantsWithoutSecretFriend,
    setShuffledparticipantsWithoutSecretFriend,
  ] = useState([])

  useEffect(() => {
    const shuffleArray = (array: never[]) => {
      const arrayCopy = [...array]
      for (let i = arrayCopy.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1))
        const temp = arrayCopy[i]
        arrayCopy[i] = arrayCopy[j]
        arrayCopy[j] = temp
      }
      return arrayCopy
    }

    setShuffledparticipantsWithoutSecretFriend(
      shuffleArray(
        participants.others.filter(
          (participant: { name: any }) =>
            participant.name.trim() !== participants.current.friend.trim()
        )
      )
    )

    setSecretFriend(
      participants.others.find(
        (participant: { name: any }) =>
          participant.name.trim() === participants.current.friend.trim()
      )
    )
  }, [])

  const goToAdvertisePage = (link, category, action, label) => {
    analyticsTrack(category, action, label)
    window.open(link, "_blank", 'rel="noreferrer"')
  }

  const [translateValue, setTranslateValue] = useState(0)
  const [goRight, setGoRight] = useState(1)
  const carouselRef = useRef(null)
  const dragStartRef = useRef(0)

  useEffect(() => {
    const interval = setInterval(() => {
      setTranslateValue(translateValue + 220 * goRight)

      if (translateValue >= 660) {
        setGoRight(-1)
      } else if (translateValue <= 0) {
        setGoRight(1)
      }

      console.log(goRight)
      console.log(translateValue)
    }, 2000)

    return () => clearInterval(interval)
  }, [translateValue])

  const handleMouseDown = (e) => {
    dragStartRef.current = e.clientX
    document.addEventListener("mousemove", handleMouseMove)
    document.addEventListener("mouseup", handleMouseUp)
  }

  const handleMouseMove = (e) => {
    const dragDistance = (e.clientX - dragStartRef.current) * 20
    setTranslateValue(translateValue + dragDistance)
    dragStartRef.current = e.clientX
  }

  const handleMouseUp = () => {
    document.removeEventListener("mousemove", handleMouseMove)
    document.removeEventListener("mouseup", handleMouseUp)
  }

  // User-initiated dragging on touch start
  const handleTouchStart = (e) => {
    dragStartRef.current = e.touches[0].clientX
  }

  const handleTouchMove = (e) => {
    const dragDistance = e.touches[0].clientX - dragStartRef.current
    setTranslateValue(translateValue + dragDistance)
    dragStartRef.current = e.touches[0].clientX
  }

  return (
    <>
      <section className="banner">
        <div className="hero">
          <div className="valueProposition">
            Amigo secreto sem dobrar papelzinho
          </div>
          <img src={Iphones} alt="Papelzinho logo" className="iphones" />
        </div>
      </section>
      <div className="page-container">
        <div className="pageContent">
          <section className="mb-4">
            <div className="groupInfoContainer">
              <p className="welcomeText">Olá {participants.current.name},</p>

              <div className="groupNameAndMembersNumber">
                <div className="groupTextName">
                  Você está no <span className="groupName">{group.name}</span>
                </div>

                <span className="group__participants">
                  {participants.others.length + 1} participantes
                </span>
              </div>

              <p className="group__description">{group.comments}</p>

              <div className="showFriendButtonContainer">
                <button className="show-friend-btn" onClick={showFriendOnClick}>
                  Revelar meu amigo secreto
                </button>
              </div>
            </div>
          </section>

          <div className="groupFriendAndYouRow">
            <Participant participant={secretFriend} isSecretFriend={true} />
            <ParticipantForm participant={participants.current} />
          </div>

          <OutboundLink
            href="https://meliuz.onelink.me/2657008059/lfxerda1"
            target="_blank"
            onClick={() => {
              analyticsTrack("home-acesso-méliuz", "clique", "banner-méliuz")
            }}
          >
            <div className="adSeparator"></div>

            <div className="adSeparatorMobileContainer">
              <div className="adSeparatorMobile"></div>
            </div>
          </OutboundLink>

          <div className="separator"></div>

          <p className="othersParticipantsChoicesTitle">
            As escolhas dos outros participantes
          </p>

          <div className="row otherParticipants">
            {shuffledparticipantsWithoutSecretFriend.map(
              (participant, index) => (
                <Participant key={index} participant={participant} />
              )
            )}
          </div>
        </div>

        <div className="reviewsContainer">
          <div className="reviewsTitle">Veja as avaliações dos usuários</div>

          <div
            className="reviews-carousel-container"
            onMouseDown={handleMouseDown}
            onTouchStart={handleTouchStart}
            onTouchMove={handleTouchMove}
          >
            <div
              className="reviews"
              style={{ transform: `translateX(${translateValue}px)` }}
              ref={carouselRef}
            >
              <img src={Review1} className="reviewImg" />
              <img src={Review2} className="reviewImg" />
              <img src={Review3} className="reviewImg" />
              <img src={Review4} className="reviewImg" />
              <img src={Review5} className="reviewImg" />
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

const SorteioPage: FC = () => {
  const initialState: State = {
    loading: true,
  }
  const [state, setState] = useState(initialState)

  const [showPopup, setShowPopup] = useState(false)
  const [showSecretFriend, setShowSecretFriend] = useState(false)

  const showFriend: () => void = () => {
    setShowSecretFriend(true)
    setShowPopup(true)
  }

  const hideFriend = () => {
    analyticsTrack("cancel-button", "clique", "Campanha Méliuz")
    setShowPopup(false)
  }

  useEffect(() => {
    const query = new URLSearchParams(window.location.search)
    const groupId = query.get("g")
    const personId = query.get("p")

    fetch(
      `https://lm4ipcuu6k.execute-api.us-west-2.amazonaws.com/prd/participant/${groupId}/${personId}`
    )
      .then((response) => response.json())
      .then((data) => {
        if (data.message === "Group not found") {
          alert(
            "Aparentemente o seu grupo de amigo secreto foi deletado pelo organizador no app. Caso esta mensagem esteja errada e seu grupo não tenha sido excluído, entre em contato com o suporte através do meajuda@papelzinho.com.br ou pelo chat na nossa página do facebook."
          )
          return
        } else if (data.message === "") {
          alert(
            "Participante não encontrado. Entre em contato com o suporte através do meajuda@papelzinho.com.br ou pelo chat na nossa página do facebook."
          )
          return
        }

        const participants = data.participants.reduce(
          (acc: any, participant: { current: any }) => {
            const newMap = acc
            if (participant.current) {
              newMap["current"] = {
                ...participant,
                groupId,
              }
            } else if (newMap["others"]) {
              newMap["others"].push(participant)
            } else {
              newMap["others"] = [participant]
            }
            return newMap
          },
          {}
        )

        setState({
          loading: false,
          participants,
          group: { name: data.name, comments: data.comments },
        })
        setShowPopup(true)
      })
      .catch(() => {
        alert(
          "Sistema fora do ar. Estamos trabalhando para corrigir o problema o mais rápido possível. Tente acessar novamente mais tarde."
        )
      })
  }, [false])

  return (
    <Layout>
      <Helmet>
        <title>Sorteio | Papelzinho ™</title>
      </Helmet>

      {state.loading ? (
        <LoadingScreen />
      ) : (
        <Content
          showFriendOnClick={showFriend}
          group={state.group}
          participants={state.participants}
        />
      )}

      {showPopup && (
        <Popup
          message={`"${state.participants.current.friend}"`}
          action2={hideFriend}
          showSecretFriend={showSecretFriend}
        />
      )}
    </Layout>
  )
}

export default SorteioPage
